<script>
import {
  getReturnListService,
  setReturnStatusService
} from "../../services/commerceService.js";
import { store, storage } from "../../utils/store";
import eventBus from '../../utils/eventBus';
import log from '../../utils/log';
import NavigationBar from "../title/NavigationBar";
export default {
  name: "commerceReturnList",
  components: { NavigationBar },
  data() {
    return {
      orderList: [],
      isLoading: false,
      finished: false,
      orderData: { page: 1, row: 10 },
      active: 0,
      imgDefault: require("../../assets/imgs/commerce/img_Default@2x.png"),
      serachIcon: require("../../assets/imgs/commerce/icon_Shape.png"),
      userInfo: null,
      scrollTop: 0,
      // 已驳回
      rejectImage: "url(" + (require("../../assets/imgs/commerce/bg_turndown_state.png")) + ") ",
      // 已完成
      finishImage: "url(" + require("../../assets/imgs/commerce/bg_complete_state.png") + ") ",
      // 待退款
      refundImage:"url(" +  require("../../assets/imgs/commerce/bg_Waiting_state_2.png") + ") ",
      // 待审核
      checkImage: "url(" + require("../../assets/imgs/commerce/bg_Waiting_state_1.png") + ") ",
    };
  },
  beforeRouteLeave(to, from, next) {
    this.scrollTop = document.querySelectorAll('.van-tab__pane')[this.active].scrollTop;
    next();
  },
  activated() {
    //获取用户数据
    this.userInfo = store.get("userInfo");
    this.userInfo = this.userInfo
      ? this.userInfo
      : storage.getStorage("custNews");
    this.orderData.recAreaCode = this.userInfo.AREA_CODE;
    if (JSON.stringify(this.$route.params) != "{}") {
      if(this.active != this.$route.params.orderInfo) {
        // 这里会执行onTabChange方法
        this.active = this.$route.params.orderInfo * 1;
      } else {
        this.initParams();
        this.getReturnList();
      }
      document.querySelectorAll('.van-tab__pane')[this.active].scrollTop = 0;
    } else {
      document.querySelectorAll('.van-tab__pane')[this.active].scrollTop = this.scrollTop;
    }
  },
  methods: {
    //初始化数据
    initParams() {
      this.orderData.page = 1;
      this.orderList = [];
      this.finished = false;
    },
    //获取退货列表方法
    getReturnList() {
      let custNews = (store.get("custNews") && store.get("custNews").AREA_CODE) ?
              store.get("custNews") : storage.getStorage("custNews");
      if (this.userInfo.AREA_CODE == "" || this.userInfo.AREA_CODE == undefined) {
        log.i("area code empty, reset first.");
        this.userInfo = (custNews && custNews.AREA_CODE) ? custNews : storage.getStorage("custNews");
        this.orderData.recAreaCode = this.userInfo.AREA_CODE;
      }
      if (this.userInfo.AREA_CODE != custNews.AREA_CODE) {
        log.i("area code changed, reset first.");
        this.userInfo = (custNews && custNews.AREA_CODE) ? custNews : storage.getStorage("custNews");
        this.orderData.recAreaCode = this.userInfo.AREA_CODE;
      }
      this.orderData.orderCate = this.active;
      this.isLoading = true;
      getReturnListService(this.orderData)
        .then(result => {
          //关闭加载动画
          this.isLoading = false;
          if (result.resultCode == "0") {
              //最后一页，结束分页加载
              if (result.pages <= this.orderData.page || result.pages == 0) {
                this.finished = true;
              } else {
                this.finished = false;
                this.orderData.page++;
              }
              setTimeout(() => {
                //返回值循环加入数组中，只有push才能vue识别
                this.orderList = this.orderList.concat(result.data)
              }, 200);
          } else {
            //加载失败后，关闭加载动画
            this.finished = true;
            result.msg
                  ? this.$toast(result.msg)
                  : this.showMsg("网络异常，请稍后重试！");
          }
        })
        .catch(error => {
          //加载失败后，关闭加载动画
          this.isLoading = false;
          this.finished = true;
          this.$toast("加载失败，请稍后重试！");
        });
    },
    //分页加载
    loadMore() {
        this.getReturnList();
    },
    adoptReturn(orderInfo) {
      this.setReturnStatus(orderInfo.returnBillNo, 1);
    },
    rejectReturn(orderInfo) {
      this.setReturnStatus(orderInfo.returnBillNo, 0);
    },
    setReturnStatus(returnBillNo, auditResult) {
      var requestData = {
        returnBillNo: returnBillNo,
        auditResult: auditResult
      };
      setReturnStatusService(requestData)
        .then(result => {
          //请求成功
          if (result.resultCode == "0") {
            this.changeDataFromList(returnBillNo, auditResult ? "已完成" : "已驳回")
            this.$toast("审批成功");
          } else {
            this.$toast("审批失败，请重试！");
          }
        })
        .catch(error => {
          this.$toast("网络异常，请稍后重试！");
        });
    },
    changeDataFromList(returnBillNo, status) {
      var index = -1;
      for(var i = 0; i < this.orderList.length; i++) {
        if(this.orderList[i].returnBillNo == returnBillNo) {
          index = i;
          break;
        }
      }
      if(this.active == 0) {   // 全部
        this.orderList[index].billStatus = status;
      } else if (this.active == 1) {   // 待审核
        this.orderList.splice(index, 1);
      } else {   // 刷新
        this.initParams();
        this.getReturnList();
      }
      eventBus.$emit('reloadSaleNum');
    },
    //进入退货详情
    gotoReturnDetail(order) {
      store.set("returnDealFunction", this.changeDataFromList);
      this.$router.push({
        path: "/commerceReturnDetail",
        query: {
          orderInfo: order
        }
      });
    },
    gotoSearch() {
      store.set("returnDealFunction", this.changeDataFromList);
      this.$router.push({
        path: "/commerceSearch",
        query: {
          orderType: 2
        }
      });
    },
    onClickLeft() {
      store.dele("returnDealFunction");
      this.$router.back();
    },
    onTabChange(index) {
      this.initParams();
      this.getReturnList();
    }
  }
};
</script>

<style>
.commerceReturnList {
  background-color: #f2f2f2;
  height: 100%;
  text-align: left;
}
/* 重新tab和tabs样式 */
.commerce-return-tabs-box {
  position: fixed;
  top: 90px;
  width: 100%;
  z-index: 90;
  bottom: 0;
}
.commerce-return-tabs-box .van-tabs {
  height: 100%!important;
  background: #ffffff;
}
.commerce-return-tabs-box .van-tabs .van-tabs__wrap {
  border: 0 !important;
}
.commerce-return-tabs-box .van-hairline--top-bottom::after, .commerce-return-tabs-box .van-hairline-unset--top-bottom::after {
  border: 0 !important;
}
.commerce-return-tabs-box .van-tabs__content {
  position: absolute;
  top: 38px;
  bottom: 0;
  box-sizing: border-box;
  width: 100%;
}
.commerce-return-tabs-box .van-tabs__content .van-tab__pane {
  height: 100%;
  overflow: auto;
  background-color: #f2f2f2;
}
.commerce-return-tabs-box .van-tabs .van-tabs__nav {
  height: 38px !important;
  background: transparent !important;
}
.commerce-return-tabs-box .van-tab {
  height: 38px !important;
}
/* 列表搜索 */
